.main_div {
  width: 100%;
  display: flex;
  justify-content: center;
  height: 50%;
  align-items: center;
}

.Card {
  background-color: white;
  width: 350px;
  /* height: 230px; */
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  justify-content: space-between;
  border: none;
  box-shadow: 4px 4px 8px grey;
  padding: 15px;
}

.inp1 {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border: none;
  padding: 5px;
  margin-bottom: 5px;
  font-size: 30px;
}

.inp2 {
  height: 130px;
  width: 100%;
  border: none;
  padding: 5px;
}

.btn_add {
  margin: 5px;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  border: none;
  font-size: 20px;
}

.btn_add:hover {
  background-color: rgb(61, 141, 61);
  color: white;
}
