.Detail {
  background-color: rgba(166, 219, 177, 0.568);
  height: 200px;
  width: 200px;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  border-radius: 5px;
  padding: 5px;
  margin: 0px 20px;
  box-shadow: 3px 3px 5px grey;
}

h3 {
  width: 100%;
  display: flex;
  justify-content: center;
  color: rgb(124, 81, 81);
}

h5 {
  width: 100%;
  display: flex;
  height: 100px;
  justify-content: center;
  color: grey;
}

.btn_delete {
  background-color: white;
  color: red;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  border: none;
}

.btn_delete:hover {
  background-color: red;
  color: white;
}
